import Sidebar from "@/components/MasterTeacher/Sidebar";
import Header from "@/components/MasterTeacher/Header";
// Button Components
import PrimaryButton from "@/components/Common/Buttons/PrimaryButton";
import SecondaryButton from "@/components/Common/Buttons/SecondaryButton";
import UtilityButton from "@/components/Common/Buttons/UtilityButton";
import DangerButton from "@/components/Common/Buttons/DangerButton";
// Text Components
import SecondaryHeader from "@/components/Common/Texts/SecondaryHeader";
import TertiaryHeader from "@/components/Common/Texts/TertiaryHeader";
import BodyText from "@/components/Common/Texts/BodyText";

// Analytics Components
function AnalyticsCard({
  title,
  value,
  change,
  changeType = "positive",
  icon,
  className = "",
}: {
  title: string;
  value: string | number;
  change?: string;
  changeType?: "positive" | "negative" | "neutral";
  icon?: React.ReactNode;
  className?: string;
}) {
  const changeColor = {
    positive: "text-green-600",
    negative: "text-red-600",
    neutral: "text-gray-600"
  };

  return (
    <div className={`bg-white rounded-lg border border-gray-200 p-4 shadow-sm ${className}`}>
      <div className="flex items-center justify-between">
        <div>
          <p className="text-sm font-medium text-gray-600">{title}</p>
          <p className="text-2xl font-bold text-gray-900 mt-1">{value}</p>
          {change && (
            <p className={`text-sm mt-1 ${changeColor[changeType]}`}>
              {change}
            </p>
          )}
        </div>
        {icon && (
          <div className="text-gray-400">
            {icon}
          </div>
        )}
      </div>
    </div>
  );
}

function ProgressBar({
  label,
  value,
  max,
  color = "bg-blue-500"
}: {
  label: string;
  value: number;
  max: number;
  color?: string;
}) {
  const percentage = Math.round((value / max) * 100);

  return (
    <div className="mb-3">
      <div className="flex justify-between text-sm mb-1">
        <span className="font-medium text-gray-700">{label}</span>
        <span className="text-gray-600">{value}/{max} ({percentage}%)</span>
      </div>
      <div className="w-full bg-gray-200 rounded-full h-2">
        <div
          className={`h-2 rounded-full ${color} transition-all duration-300`}
          style={{ width: `${percentage}%` }}
        ></div>
      </div>
    </div>
  );
}

function AttendanceChart() {
  const attendanceData = [
    { day: "Mon", present: 85, absent: 15 },
    { day: "Tue", present: 92, absent: 8 },
    { day: "Wed", present: 78, absent: 22 },
    { day: "Thu", present: 88, absent: 12 },
    { day: "Fri", present: 95, absent: 5 },
  ];

  return (
    <div className="bg-white rounded-lg border border-gray-200 p-4 shadow-sm">
      <h3 className="text-lg font-semibold text-gray-900 mb-4">Weekly Attendance Overview</h3>
      <div className="space-y-3">
        {attendanceData.map((data) => (
          <div key={data.day} className="flex items-center">
            <div className="w-8 text-sm font-medium text-gray-600">{data.day}</div>
            <div className="flex-1 ml-3">
              <div className="flex justify-between text-xs mb-1">
                <span>Present: {data.present}%</span>
                <span>Absent: {data.absent}%</span>
              </div>
              <div className="w-full bg-gray-200 rounded-full h-2">
                <div
                  className="bg-green-500 h-2 rounded-l-full"
                  style={{ width: `${data.present}%` }}
                ></div>
                <div
                  className="bg-red-500 h-2 rounded-r-full ml-auto"
                  style={{ width: `${data.absent}%`, marginLeft: `-${data.absent}%` }}
                ></div>
              </div>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}

function StudentPerformanceChart() {
  const performanceData = [
    { grade: "Grade 4", excellent: 45, good: 35, needsImprovement: 20 },
    { grade: "Grade 5", excellent: 52, good: 32, needsImprovement: 16 },
    { grade: "Grade 6", excellent: 38, good: 42, needsImprovement: 20 },
  ];

  return (
    <div className="bg-white rounded-lg border border-gray-200 p-4 shadow-sm">
      <h3 className="text-lg font-semibold text-gray-900 mb-4">Student Performance by Grade</h3>
      <div className="space-y-4">
        {performanceData.map((data) => (
          <div key={data.grade}>
            <div className="flex justify-between text-sm mb-2">
              <span className="font-medium text-gray-700">{data.grade}</span>
              <span className="text-gray-600">Total: {data.excellent + data.good + data.needsImprovement}</span>
            </div>
            <div className="flex h-4 rounded-full overflow-hidden">
              <div
                className="bg-green-500"
                style={{ width: `${(data.excellent / (data.excellent + data.good + data.needsImprovement)) * 100}%` }}
              ></div>
              <div
                className="bg-yellow-500"
                style={{ width: `${(data.good / (data.excellent + data.good + data.needsImprovement)) * 100}%` }}
              ></div>
              <div
                className="bg-red-500"
                style={{ width: `${(data.needsImprovement / (data.excellent + data.good + data.needsImprovement)) * 100}%` }}
              ></div>
            </div>
            <div className="flex justify-between text-xs mt-1">
              <span className="text-green-600">Excellent: {data.excellent}</span>
              <span className="text-yellow-600">Good: {data.good}</span>
              <span className="text-red-600">Needs Work: {data.needsImprovement}</span>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}

function RecentActivities() {
  const activities = [
    { time: "2 hours ago", action: "Added new student: John Doe", type: "student" },
    { time: "4 hours ago", action: "Updated attendance for Grade 5", type: "attendance" },
    { time: "6 hours ago", action: "Created remedial session for Mathematics", type: "remedial" },
    { time: "1 day ago", action: "Uploaded new learning material", type: "material" },
    { time: "2 days ago", action: "Reviewed teacher performance report", type: "teacher" },
  ];

  const getActivityIcon = (type: string) => {
    switch (type) {
      case "student":
        return <div className="w-2 h-2 bg-blue-500 rounded-full"></div>;
      case "attendance":
        return <div className="w-2 h-2 bg-green-500 rounded-full"></div>;
      case "remedial":
        return <div className="w-2 h-2 bg-purple-500 rounded-full"></div>;
      case "material":
        return <div className="w-2 h-2 bg-orange-500 rounded-full"></div>;
      case "teacher":
        return <div className="w-2 h-2 bg-indigo-500 rounded-full"></div>;
      default:
        return <div className="w-2 h-2 bg-gray-500 rounded-full"></div>;
    }
  };

  return (
    <div className="bg-white rounded-lg border border-gray-200 p-4 shadow-sm">
      <h3 className="text-lg font-semibold text-gray-900 mb-4">Recent Activities</h3>
      <div className="space-y-3">
        {activities.map((activity, index) => (
          <div key={index} className="flex items-start space-x-3">
            <div className="mt-1">
              {getActivityIcon(activity.type)}
            </div>
            <div className="flex-1">
              <p className="text-sm text-gray-900">{activity.action}</p>
              <p className="text-xs text-gray-500">{activity.time}</p>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}

